.float-shadow {
    filter: drop-shadow(5px 5px 5px #666341);
    animation-name: floatShadowAnimation;
    animation-iteration-count: infinite;
    animation-duration: 5000ms;
    animation-timing-function: linear;
    animation-direction: reverse;
    animation-fill-mode: both;
}
@keyframes floatShadowAnimation {
    0% {
        filter: drop-shadow(5px 5px 5px #64613d);
    }
    25% {
        filter: drop-shadow(5px 5px 5px #716d41);
    }
    50% {
        filter: drop-shadow(5px 5px 5px #fdb100);
    }
    75% {
        filter: drop-shadow(5px 5px 5px #53502a);
    }
    100% {
        filter: drop-shadow(5px 5px 5px #64613d);
    }
}
